import DataTableZ from "../components/dataTable";
import "../../../../common/extensions/global";
import Global from "../components/Global";
import tinyMceLoader from "../../../../common/TinyMceHelper";
import "../../scss/pages/comments.scss";

(() => {

    enum ReadStatus {
        UnRead = 1,
        Read = 2,
        Reply = 3
    }

    let subCommentReplyApi = "/api/EssayComment/ReplyUser";
    let softDeleteApi = "/api/EssayComment/Delete";
    let readApi = "/api/EssayComment/Read";
    let replyApi = "/api/EssayComment/Replied";
    let toPublucApi = "/api/EssayComment/ToPublic";
    let toSecretApi = "/api/EssayComment/ToSecret";
    let essayInfoApi = "/api/EssayComment/EssayInfo";

    //所有文章列表请求
    $.ajax({
        url: essayInfoApi,
        type: "get",
        success: (msg: any) => {
            if (msg.Code === 0) {
                let data = msg.Data;
                let optionsHtml = data.map((item: any) => {
                    return `<option value="${item.Id}">${item.Title}</option>`
                }).join(" ");
                $("#essayTitle").html(optionsHtml).selectpicker('refresh');
            }
        }
    });

    let dt = new DataTableZ();
    const eventManager: any = (action: string, idArr: Array<string | number> = null) => {
        if(!idArr){
            idArr = dt.selectIdArr;
        }
        if(idArr.length === 0){
            return;
        }
        switch (action.toLocaleLowerCase()) {
            case "del":
                Global.showLoading();
                let result = DataTableZ.defautlDelete
                    (dt, softDeleteApi,
                        idArr,
                        (msg: any) => {
                            dt.reload();
                            Global.hideLoading();
                            dt.reload();
                        },
                        (jqXHR: any, textStatus: string, errorThrown: string) => {
                            console.log(jqXHR, textStatus, errorThrown);
                            alert("网络错误");
                            Global.hideLoading();
                        });
                if (!result) {
                    Global.hideLoading();
                }
                break;
            case "read":
                $.post(readApi, {
                    idArr: idArr
                }, (msg:any)=>{
                    dt.reload();
                });
                break;
            case "reply":
                $.post(replyApi, {
                    idArr: idArr
                }, (msg:any)=>{
                    dt.reload();
                });
                break;

            case "topublic":                
                $.post(toPublucApi, {
                    idArr: idArr
                }, (msg:any)=>{
                    dt.reload();
                });
                break;
            case "tosecret":
                $.post(toSecretApi, {
                    idArr: idArr
                }, (msg:any)=>{
                    dt.reload();
                });
                break;
            default:
                break;
        }
    }

    const bindEvent = () => {
        $(".table-tool .filter").click((e: any) => {
            DataTableZ.defaultFilterEvent(dt);
        });

        $(".table-tool .reset").click((e: any) => {
            dt.resetFilter();
        });

        $(".del").click(() => {
            eventManager("del");
        });

        $(".readed").click(() => {
            eventManager("read");
        });
        $(".reply").click(() => {
            eventManager("reply");
        }); 
        $(".public").click(() => {
            eventManager("topublic");
        });

        $(".secret").click(() => {
            eventManager("tosecret");
        }); 
    }

    /**每次重绘后为状态按钮添加事件 */
    const bindDrawCallBackEvent = () => {

        console.log("bindDrawCallBackEvent");
        $("tbody .row-del").on("click", (e: any) => {
            e.preventDefault();
            let id = $(e.target).data("id");
            eventManager("del", [id]);
        })
    }
    
    let dtZConfig = {
        selector: "#dataTable",
        columnDefs: [
            {
                targets: 3,
                visible: false
            },
            {
                targets: 7,
                visible: false
            },
            //{
            //    targets: 8,
            //    visible: false
            //}
        ],
        "order": [[1, "desc"]],
        columns: [
            DataTableZ.checkBoxCol(),//0
            {
                "data": "Create", "title": "创建时间",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss");
                },
            },
            {
                "data": "NickName", "title": "昵称", width: "200px",
                render: (data: any, type: any, row: any, meta: any) => {
                    let html = data;
                    switch (row.ReadStatus) {
                        case ReadStatus.UnRead:
                            html = `<span class="badge badge-secondary">未读</span>${html}`;
                            break;
                        case ReadStatus.Read:
                            html = `<span class="badge badge-info">已读</span>${html}`;
                            break;
                        case ReadStatus.Reply:
                            html = `<span class="badge badge-primary">已回复</span>${html}`;
                            break;
                        default:
                            break;
                    }
                    return html;
                },
            },
            {
                "data": "IsNotifyBeReply", "title": "是否通知",
                render: (data: any, type: any, row: any, meta: any) => {
                    return data;
                }
            },
            {
                "data": "IsDelete", "title": "删除状态",
                render: (data: any, type: any, row: any, meta: any) => {
                    if (data) {
                        return '<span class="badge badge-danger">true</span>';
                    }
                    return '<span class="badge badge-info">false</span>';;
                }
            },
            {
                "data": "Title", "title": "文章"               
            },
            {
                "data": "IsSecret", "title": "私密",
                render: (data: any, type: any, row: any, meta: any) => {
                    if(data){
                        return `<span class="badge badge-dark secret" data-secret="${data}" data-id="${row["Id"]}">${data}</span>`;
                    }else{
                        return `<span class="badge badge-light secret" data-secret="${data}" data-id="${row["Id"]}">${data}</span>`;                        
                    }
                }
            },
            //{
            //    "data": "UaMeta_BrowserName", "title": "浏览器"
            //},
            //{
            //    "data": "UaMeta_OsName", "title": "操作系统"
            //},
            {
                "data": "Id", "title": "操作",
                render: (data: any, type: any, row: any, meta: any) => {
                    return `<div class="btn-group ">
                    <button class="btn btn-info btn-sm text-white see-comment">查看</button>
                    <button class="btn btn-secondary btn-sm text-white row-reply">回复</button>
                    <button class="btn btn-danger btn-sm row-del" data-id="${data}">删除</button>                    
                </div>`;
                }
            }
        ],
        ajaxUrl: "/api/EssayComment/Table",
        initComplete: () => {
            bindEvent();
        },
        afterDraw: (setting: any, data: Array<any>) => {
            bindDrawCallBackEvent();

            //查看评论
            $(".see-comment").click(function () {
                var $tr = $(this).parents("tr");
                let id = parseInt(<string>$tr.find(".row-selector>input").val());
                var rowData = data.filter((item: any) => {
                    console.log(item.EssayId, id, item.EssayId === id);
                    return item.Id === id;
                })[0];
                if (rowData.ReadStatus === ReadStatus.UnRead) {
                    eventManager('read', [id]);
                }
                var htmlStr = rowData["Content"];
                $("#myModal .modal-body").html(htmlStr);
                $('#myModal').modal('show');
            });
            //回复按钮被点击
            $(".row-reply").click((e: any) => {
                var $tr = $(e.target).parents("tr");
                let id = parseInt(<string>$tr.find(".row-selector>input").val());

                var rowData = data.filter((item: any) => {
                    console.log(item.EssayId, id, item.EssayId === id);
                    return item.Id === id;
                })[0];
                if (rowData.ReadStatus !== ReadStatus.Reply) {
                    if (rowData.ReadStatus === ReadStatus.UnRead) {
                        eventManager('reply', [id]);
                    }
                }

                $('#replyModal').data("replyId", id);
                $('#replyModal').data("essayId", rowData.EssayId);
                $('#replyModal').modal('show');
            })
            //私密还是公开
            $(".secret").click((e: any) => {
                let $this = $(e.target);
                let id = $this.data("id");
                let secret = $this.data("secret");
                if (secret) {
                    eventManager('ToPublic', [id]);
                } else {
                    eventManager('ToSecret', [id]);
                }

            })
        }
    };

    dt.render(dtZConfig);


    tinyMceLoader.init("#tinyEditor","full","300px",'100%',{
        toolbar_mode: 'wrap'
    });

    /**
     * 确认回复
     */
    $("#submitReply").click(function () {
        var content = tinyMceLoader.getContent("tinyEditor");
        if (!content) {
            alert("回复内容不能为空！");
            return;
        }

        var obj: any = {};
        obj.commentId = $('#replyModal').data("replyId");
        obj.content = content;
        obj.essayId = $('#replyModal').data("essayId");
        Global.showLoading();
        $.ajax({
            url: subCommentReplyApi,
            data: obj,
            type: "POST",
            dataType: "JSON",
            async: true,
            success(msg) {
                if (msg.Code === 0) {
                    alert("回复成功");
                } else {
                    Global.alertError(msg.Error);
                }
            },
            error() {
                alert("网络错误！");
            },
            complete() {
                Global.hideLoading();
            }
        });

        $('#replyModal').modal('hide');
    });

})();